export default {
  props: {
    // 每排的间距
    minimumLineSpacing: {
      type: Number
    },
    // 每列的间距
    minimumInteritemSpacing: {
      type: Number
    },
    dataArr: {
      type: Array
    }
  },
  data() {
    return {}
  },
  created() {},
  methods: {
    gotoAppDetail(item) {
      this.$emit('gotoAppDetail', item)
    },
    getIcon(item) {
      return `${this.axios.defaults.baseURL}${item.icon}`
    },
    getShortUrl(item) {
      return `${this.axios.defaults.baseURL}${item.shortUrl}`
    }
  }
}



