export default {
  data() {
    return {
      currentpwd: '',
      newpwd: '',
      renewpwd: '',
      errorInfo: ''
    }
  },
  created() {
  },
  methods: {
    clickChangeBtn() {
      if (this.currentpwd === '') {
        this.errorInfo = '*请输入当前密码'
        return
      }
      if (this.newpwd === '') {
        this.errorInfo = '*请输入新密码'
        return
      }
      if (this.renewpwd === '') {
        this.errorInfo = '*请输入确认密码'
        return
      }
      if (this.renewpwd !== this.newpwd) {
        this.errorInfo = '*两次输入密码不一致'
        return
      }
      this.errorInfo = ''
      console.log()
    }
  }
}



