import {getUserInfo} from './mgr/userMgr'

export default {
  name: 'App',
  data() {
    return {
    }
  },
  created() {
    this.$nextTick(() => {
      let user = getUserInfo()
      if (user) {
        // 防止新打开的窗口也push到列表页
        if (window.location.href === (window.location.origin + '/')) {
          this.$router.push('/apps')
        }
      } else {
        this.$router.push('/login')
      }
    })
  },
  components: {
  },
  methods: {
  }
}



