import { Message } from 'element-ui'
const axios = require('axios')

export default {
  props: {
    appFile: {
      type: FileList
    },
    teamId: {
      type: String
    }
  },
  data() {
    return {
      progress: 0,
      source: null // 取消上传
    }
  },
  created() {
    this.$nextTick(() => {
      this.beginLoad()
    })
  },
  methods: {
    cancelUpload() {
      if (this.source) {
        this.source.cancel('取消上传')
      }
      this.$emit('closeUpload')
    },
    beginLoad() {
      const _this = this

      // 取消上传使用
      let cancelToken = axios.CancelToken
      _this.source = cancelToken.source()

      var data = new FormData()
      data.append('file', this.appFile[0])
      var config = {
        onUploadProgress: function(progressEvent) {
          var percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
          _this.$nextTick(() => {
            _this.progress = percentCompleted
          })
        },
        cancelToken: _this.source.token
      }

      axios.defaults.headers.common['Content-Type'] = 'multipart/form-data'

      axios.post(`api/apps/${_this.teamId}/upload`, data, config)
        .then(function (res) {
          console.log(res)
          Message({
            message: '上传成功',
            type: 'success'
          })
          _this.$emit('uploadSuccess')
        })
        .catch(function(err) {
          if (axios.isCancel(err)) {
            console.log('Request canceled', err.message)
            return
          }
          Message.error(err.response.data.message)
        })
    }
  }
}



